:root {
  --primary-text-color: #000000;
  --navigation-text-color: #212121;
  --secondary-text-color: #757575;
  --svg-color: #afb1b8;
  --accent-color: #2196f3;
  --accent-color-text: #ffffff;
  --section-bg-color: #f5f4fa;
  --address-bg-color: #2f303a;
  --buttons-bg: #f5f4fa;
  --main-font-family: Roboto, sans-serif;
  --main-logo-font: Raleway, sans-serif;
  --animation-timing: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  --letter-spacing-main: 0.03em;
  --letter-spacing-secondary: 0.06em;
  --letter-spacing-minimal: 0.01em;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: normal;
  font-style: normal;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: var(--primary-text-color);
}

img {
  display: block;
}

body {
  background-color: #ffffff;
  color: var(--primary-text-color);
  font-family: var(--main-font-family);
}

address {
  font-style: normal;
}

.section {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .section {
    width: 480px;
    padding: 0;
  }
}
@media screen and (min-width: 768px) {
  .section {
    width: 768px;
  }
}
@media screen and (min-width: 1200px) {
  .section {
    width: 1200px;
  }
}
.backdrop.is-hidden,
header {
  display: none;
}

.social {
  display: inline-flex;
  height: 44px;
}
.social-item {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.social-item:not(:last-child) {
